import { NgModule, InjectionToken } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatButtonModule } from '@angular/material/button';
import { MatChipsModule } from '@angular/material/chips';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatRadioModule } from '@angular/material/radio';
import { MatSelectModule } from '@angular/material/select';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatTooltipModule } from '@angular/material/tooltip';
import {
  NotificationsModule
} from '@verdocs/notifications'
import { TokensModule } from '@verdocs/tokens';
import { ProfilesModule } from '@verdocs/profiles';
import { RecipientEditorComponent } from './template/recipients/recipients-editor.component'
import { TemplateSourceComponent } from './template/source-file/source-file.component'
import { VisibilityEditorComponent } from './template/visibility/visibility-editor.component'
import { HeaderComponent } from './header.component';
import { VerdocsHeaderService } from './services/header.service';
import { TemplatesGuardService } from './services/templates.guard';
import { HeaderToolsComponent } from './components/header-tools.component';
import { FourOhOneDialog } from './error-dialogs/four-oh-one.dialog';

export interface connectionConfig {
  rForm_backend_url: string;
  rForm_frontend_url: string;
  rSecure_frontend_url: string;
  rSecure_backend_url: string;
  rAccount_frontend_url: string;
  rAccount_backend_url: string;
  rNotification_backend_url: string;
  origin?: 'rForm' | 'rAccount' | 'rSecure';
  app_version?: string;
}

export const connectionConfigToken = new InjectionToken<connectionConfig>('connectionConfig');

@NgModule({
  declarations: [
    HeaderComponent,
    RecipientEditorComponent,
    TemplateSourceComponent,
    VisibilityEditorComponent,
    HeaderToolsComponent,
    FourOhOneDialog
  ],
  entryComponents: [
    FourOhOneDialog
  ],
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule,
    RouterModule,
    ReactiveFormsModule,
    MatSnackBarModule,
    MatIconModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatSelectModule,
    MatDialogModule,
    MatChipsModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatTooltipModule,
    NotificationsModule,
    ProfilesModule,
    TokensModule
  ],
  providers: [
    TemplatesGuardService,
    VerdocsHeaderService
  ],
  exports: [
    HeaderComponent,
    RecipientEditorComponent,
    TemplateSourceComponent,
    VisibilityEditorComponent,
    HeaderToolsComponent,
    FourOhOneDialog
  ]
})
export class HeaderModule {
  static initVerdocsHeader(config: connectionConfig) {
    return {
      ngModule: HeaderModule,
      providers: [
        {
          provide: connectionConfigToken,
          useValue: config
        }
      ]
    }
  }
}
